import { apiRoot } from './config'

export let processResource: (id?:number) => string =
    function (id?:number) {
        if(id){
            return `${apiRoot}/process/${id}`
        }
        return `${apiRoot}/process`
    }
export let processStatsResource: () => string =
    function () {
        return `${apiRoot}/process/stats`
    }